import MeetupItem from "./MeetupItem";

function MeetupList(props) {
  return (
    <ul>
      {props.meetups.map((meetup) => (
        <MeetupItem
          key={meetup.id}
          id={meetup.id}
          image={meetup.image}
          title={meetup.title}
          address={meetup.address}
          desc={meetup.desc}
        />
      ))}
    </ul>
  );
}
export default MeetupList;
